







































import Vue from "vue";
import Component from "vue-class-component";
import { Emit, Prop, Watch } from "vue-property-decorator";

@Component({
  name: "GlobalOption"
})
export default class GlobalOption extends Vue {
  @Prop({ required: true })
  value: any;

  @Emit("input")
  update() {
    this.$emit("input", this.value);
  }

  data = [
    {
      title: "Text Field Global Option",
      options: [
        "outlined",
        "rounded",
        "solo",
        "singleLine",
        "clearable",
        "flat",
        "loading",
        "dense",
        "shaped",
        "filled"
      ]
    },
    {
      title: "Select Field Global Option",
      options: [
        "readonly",
        "disabled",
        "chips",
        "multiple",
        "appendIcon",
        "appendSlot",
        "appendItemSlot",
        "prependIcon",
        "prependSlot",
        "prependItemSlot",
        "selectSlot"
      ]
    }
  ];

  updateSwitch($event: any, option: any) {
    this.value.forEach((v: any) => {
      if (Object.keys(v.component.options).includes(option)) {
        v.component.options[option] = $event;
      }
    });
  }
}
